(function () {

  angular.module('meanApp', ['ngRoute']);

  function config ($routeProvider, $locationProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'home/home.view.html',
        controller: 'homeCtrl',
        controllerAs: 'vm'
      })
      .when('/dashboard', {
        templateUrl: 'dashboard/dashboard.view.html',
        controller: 'dashboardCtrl',
        controllerAs: 'vm'
      })
      .when('/register', {
        templateUrl: '/auth/register/register.view.html',
        controller: 'registerCtrl',
        controllerAs: 'vm'
      })
      .when('/login', {
        templateUrl: '/auth/login/login.view.html',
        controller: 'loginCtrl',
        controllerAs: 'vm'
      })
      .when('/logout', {
        templateUrl: '/auth/login/login.view.html',
        controller: 'logoutCtrl',
        controllerAs: 'vm'
      })
      .when('/profile', {
        templateUrl: '/profile/profile.view.html',
        controller: 'profileCtrl',
        controllerAs: 'vm'
      })
      .when('/rolodex', {
        templateUrl: '/rolodex/rolodex.view.html',
        controller: 'rolodexCtrl',
        controllerAs: 'vm'
      })
      .when('/calendar', {
        templateUrl: '/calendar/calendar.view.html',
        controller: 'calendarCtrl',
        controllerAs: 'vm'
      })
      .when('/booklet', {
        templateUrl: '/booklet/booklet.view.html',
        controller: 'bookletCtrl',
        controllerAs: 'vm'
      })
      .when('/contract', {
        templateUrl: '/contract/contract.view.html',
        controller: 'contractCtrl',
        controllerAs: 'vm'
      })
      .otherwise({redirectTo: '/'});

    // use the HTML5 History API
    $locationProvider.html5Mode(true);
  }

  function run($rootScope, $location, authentication) {
      $rootScope.$on('$routeChangeStart', function(event, nextRoute, currentRoute) {
          if ($location.path() === '/' && authentication.isLoggedIn()) {
              $location.path('/dashboard');
          }
          
          if ($location.path() === '/profile' && !authentication.isLoggedIn()) {
              $location.path('/login');
          }
          
          if ($location.path() === '/' && !authentication.isLoggedIn()) {
              $location.path('/login');
          }
          
      });
  }
  
  angular
    .module('meanApp')
    .config(['$routeProvider', '$locationProvider', config])
    .run(['$rootScope', '$location', 'authentication', run]);

})();
(function () {
  
  angular
    .module('meanApp')
    .controller('bookletCtrl', bookletCtrl);

  bookletCtrl.$inject = ['$location', 'meanData', '$route', '$scope'];
  function bookletCtrl($location, meanData, $route, $scope) {
        var vm = this;
        vm.booklets = {};
        vm.booklet = {};
      
        meanData.getBooklets()
            .success(function(data) {
                vm.booklets = data;
            })
            .error(function (e) {
                console.log(e);
            });
      
        var fbTemplate = document.getElementById('fb-template');
        var renderedContainer = document.getElementById('rendered-form');
        $(fbTemplate).formBuilder();

        $(fbTemplate).bind("change", function(e) {
            $(fbTemplate).formRender({
              container: renderedContainer
            });
        });
      
      $('#newbooklet').click(function() {
          $('#newbookletmodal').modal();           
      });
      
      $(document).on('click', '.openBooklet', function() {
          $('#modalTitle').html($(this).parent().siblings().first().html());

          $('#render').text($(this).next().text());
          
          var fbTemplate = document.getElementById('render');
          $(fbTemplate).formRender();

          $('#openbookletmodal').modal();
      });
      
      $('.form-builder-save').click(function() {
          $('.view-data').click();
          vm.booklet.content = $('code').text();
          
          console.log('Submitting booklet ' + vm.booklet.title);
          meanData.saveBooklet(vm.booklet)
            .error(function(e){
                console.log(e);
            })
            .then(function(){
                $route.reload();
            });
      });
      
  }

})();
(function () {
  
  angular
    .module('meanApp')
    .controller('calendarCtrl', calendarCtrl);

  calendarCtrl.$inject = ['$location', 'meanData', '$route', '$scope'];
  function calendarCtrl($location, meanData, $route, $scope) {
    var vm = this;
    vm.event = {};

    meanData.getCalendar()
        .success(function(data) {
            $('#calendar').fullCalendar({
                events: data,
                header: {
                    left: 'prev,next today',
                    center: 'title',
                    right: 'month,agendaWeek,agendaDay'
                },
                timezone: 'local',
                eventClick: function(event, jsEvent, view) {
                    $('#modalTitle').html(event.title);
                    $('#eventtitle').val(event.title);
                    $('#eventstart').val(event.start.format('LLL'));
                    $('#eventend').val(event.end.format('LLL'));
//                    $('#modalBody').html(event.description);

                    $('#fullCalModal').modal();
                }
            });
        })
        .error(function (e) {
            console.log(e);
        });
      
    $('#neweventbutton').click(function(){
        $('#neweventmodal').modal();
    });
    
    $('#starttimepicker')
        .datetimepicker()
        .on("dp.change", function() {
            vm.event.start = $('#starttimepicker > input').val();
        });
      
    $('#endtimepicker')
        .datetimepicker()
        .on("dp.change", function() {
            vm.event.start = $('#endtimepicker > input').val();
        });
      
    vm.onSubmit = function () {
        console.log('Submitting events ' + vm.event.title);
        vm.event.start = moment.utc($("#starttimepicker").data("DateTimePicker").date()).valueOf();
        vm.event.end = moment.utc($("#endtimepicker").data("DateTimePicker").date()).valueOf();
        meanData.saveEvent(vm.event)
            .error(function(e){
                console.log(e);
            })
            .then(function(){
                $route.reload();
            });
    };
      
    
  }

})();
