(function () {

  angular
    .module('meanApp')
    .service('authentication', authentication);

  authentication.$inject = ['$http', '$window'];
  function authentication ($http, $window) {

    var saveToken = function (token) {
      $window.localStorage['mean-token'] = token;
    };

    var getToken = function () {
      return $window.localStorage['mean-token'];
    };

    var isLoggedIn = function() {
      var token = getToken();
      var payload;

      if(token){
        payload = token.split('.')[1];
        payload = $window.atob(payload);
        payload = JSON.parse(payload);

        return payload.exp > Date.now() / 1000;
      } else {
        return false;
      }
    };

    var currentUser = function() {
      if(isLoggedIn()){
        var token = getToken();
        var payload = token.split('.')[1];
        payload = $window.atob(payload);
        payload = JSON.parse(payload);
        return {
          email : payload.email,
          name : payload.name
        };
      }
    };

    register = function(user) {
      return $http.post('/api/register', user).success(function(data){
        saveToken(data.token);
      });
    };

    login = function(user) {
      return $http.post('/api/login', user).success(function(data) {
        saveToken(data.token);
      });
    };

    logout = function() {
      $window.localStorage.removeItem('mean-token');
    };

    return {
      currentUser : currentUser,
      saveToken : saveToken,
      getToken : getToken,
      isLoggedIn : isLoggedIn,
      register : register,
      login : login,
      logout : logout
    };
  }


})();
(function() {

  angular
    .module('meanApp')
    .service('meanData', meanData);

  meanData.$inject = ['$http', 'authentication'];
  function meanData ($http, authentication) {

    var getProfile = function () {
      return $http.get('/api/profile', {
        headers: {
          Authorization: 'Bearer '+ authentication.getToken()
        }
      });
    };
      
    var getRolodex = function () {
        return $http.get('/api/rolodex', {
            headers: {
                Authorization: 'Bearer '+ authentication.getToken()
            }
        });
    };
      
    var saveContact = function (contact) {
        return $http.post('/api/saveContact', contact, {
            headers: {
                Authorization: 'Bearer '+ authentication.getToken()
            }
        });
    };
      
    var getCalendar = function () {
        return $http.get('/api/calendar', {
            headers: {
                Authorization: 'Bearer '+ authentication.getToken()
            }
        });
    };
      
    var saveEvent = function (event) {
        return $http.post('/api/saveEvent', event, {
            headers: {
                Authorization: 'Bearer '+ authentication.getToken()
            }
        });
    };
      
    var getBooklets = function () {
        return $http.get('/api/booklets', {
            headers: {
                Authorization: 'Bearer '+ authentication.getToken()
            }
        });
    };
      
    var saveBooklet = function (booklet) {
        return $http.post('/api/saveBooklet', booklet, {
            headers: {
                Authorization: 'Bearer '+ authentication.getToken()
            }
        });
    };
      
    return {
      getProfile : getProfile,
      getRolodex : getRolodex,
      saveContact: saveContact,
      getCalendar: getCalendar,
      saveEvent  : saveEvent,
      getBooklets: getBooklets,
      saveBooklet: saveBooklet
    };
  }

})();
